/** @jsx jsx */
import { jsx } from "theme-ui";
// import SEO from "@components/seo";
// import Layout from "@components/layouts";
import React from "react";
import Container from "@components/Container";
import ResourceCard from "@components/ResourceCard";
import PlainCard from "@components/PlainCard";
import { rem } from "@src/theme";
import { mapCraft } from "./mappings";
import { graphql, Link, navigate } from "gatsby";
import TypeTag from "@components/TypeTag";
import { kebabize } from "@src/helpers/utils";
import ExternalLink from "@components/icons/link";
import ArrowRight from "@components/icons/arrowRight";
import Download from "@components/icons/download";
import { transform } from "lodash";
import arrowIllustration from "@src/assets/images/arrow.svg";
import Layout from "@components/Layout";
export default (props) => {
  const { entry } = mapCraft(props.data.craft);

  const {
    title,
    description,
    type,
    links,
    attachments,
    tags,
    authors,
    organisations,
    related,
    video,
    img,
  } = entry;
  console.log(entry);

  return (
    <Layout>
      <div>
        <div
          sx={{
            bg: "csiLightGrey",
            pt: rem(10),
          }}
        >
          <Container>
            <div
              sx={{
                backgroundImage: `url(${arrowIllustration})`,
                backgroundRepeat: "no-repeat",
                backgroundPositionX: "right",
                backgroundPositionY: "top",
                py: rem(40),
              }}
            >
              <button
                onClick={() => {
                  window.history.go(-1);
                }}
                sx={{
                  fontSize: rem(18),
                  fontWeight: 600,
                  svg: {
                    transform: "rotate(180deg) translateY(-5px)",
                    marginRight: rem(3),
                  },
                }}
              >
                <ArrowRight />
                Back
              </button>
            </div>
          </Container>
        </div>

        <div
          sx={{
            py: rem(20),
          }}
        >
          <Container>
            <div
              sx={{
                display: "flex",
                flexDirection: ["column", null, "row"],
                justifyContent: "space-between",
              }}
            >
              <div
                sx={{
                  width: ["100%", null, rem(523)],
                }}
              >
                <TypeTag type={type} />
                <h1
                  sx={{
                    fontSize: [rem(34), null, rem(54)],
                    mt: rem(10),
                    mb: [rem(20), null, rem(40)],
                    lineHeight: 1.15,
                  }}
                >
                  {title}
                </h1>

                {img && (
                  <div>
                    <img
                      src={img.src}
                      alt={img.alt}
                      sx={{
                        width: "100%",
                      }}
                    />
                  </div>
                )}

                {video && (
                  <div>
                    <iframe
                      sx={{
                        width: "100%",
                        height: [rem(200), null, rem(300)],
                      }}
                      src={video}
                      frameBorder="0"
                      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                      allowFullScreen
                    ></iframe>
                  </div>
                )}
                {description && (
                  <p
                    sx={{
                      my: 0,
                      fontSize: [rem(14), null, rem(16)],
                      lineHeight: 1.35,
                      whiteSpace: "pre-line",
                    }}
                  >
                    {description}{" "}
                  </p>
                )}
              </div>

              <div
                sx={{
                  width: ["100%", null, rem(308)],
                  pt: rem(40),
                }}
              >
                {(links && links.length) ||
                (attachments && attachments.length) ? (
                  <section>
                    <h2
                      sx={{
                        fontSize: [rem(18), null, rem(24)],
                        mb: rem(5),
                        mt: 0,
                      }}
                    >
                      Resources
                    </h2>
                    <ul
                      sx={{
                        li: {
                          fontSize: [rem(14), rem(16)],
                          fontWeight: 600,
                          mb: rem(20),
                          lineHeight: 1.4,
                        },
                        span: {
                          display: "inline-block",
                          marginRight: rem(8),
                          transform: "translateY(2px)",
                        },
                      }}
                    >
                      {attachments.map(({ filename, url }) => (
                        <li>
                          <span>
                            <Download />
                          </span>
                          {filename}
                        </li>
                      ))}
                      {links.map(({ linkURL, label }) => (
                        <li>
                          <span>
                            <ExternalLink />
                          </span>
                          {label}
                        </li>
                      ))}
                    </ul>
                  </section>
                ) : null}

                {tags && tags.length ? (
                  <ul
                    sx={{
                      display: "flex",
                      flexWrap: "wrap",
                      mt:
                        (links && links.length) ||
                        (attachments && attachments.length)
                          ? rem(40)
                          : 0,
                      mb: rem(40),
                    }}
                  >
                    {tags.map(({ title, groupHandle, slug }) => (
                      <PlainCard
                        link={`${kebabize(groupHandle)}/${slug}`}
                        label={title}
                        size="xs"
                      />
                    ))}
                  </ul>
                ) : null}
                {authors && authors.length ? (
                  <section>
                    <h2
                      sx={{
                        fontSize: [rem(18), null, rem(24)],
                        mb: rem(5),
                        mt: 0,
                      }}
                    >
                      Authors
                    </h2>
                    <ul
                      sx={{
                        display: "flex",
                        fontSize: [rem(14), rem(16)],
                      }}
                    >
                      {authors.map(({ title, uri }, index) => (
                        <React.Fragment>
                          <Link to={uri}>{title}</Link>
                          {index !== authors.length - 1 && ","}
                        </React.Fragment>
                      ))}
                    </ul>
                  </section>
                ) : null}

                {organisations && organisations.length ? (
                  <section>
                    <h2
                      sx={{
                        fontSize: [rem(18), null, rem(24)],
                        mb: rem(5),
                        mt: rem(40),
                      }}
                    >
                      Organisations
                    </h2>

                    <ul
                      sx={{
                        display: "flex",
                        fontSize: [rem(14), rem(16)],
                      }}
                    >
                      {organisations.map(({ title, uri }, index) => (
                        <React.Fragment>
                          <Link to={uri}>{title}</Link>
                          {index !== organisations.length - 1 && ","}
                        </React.Fragment>
                      ))}
                    </ul>
                  </section>
                ) : null}
              </div>
            </div>

            {related && related.length ? (
              <section
                sx={{
                  mt: [rem(80), null, rem(100)],
                }}
              >
                <h2
                  sx={{
                    fontSize: [rem(18), null, rem(24)],
                  }}
                >
                  Related content
                </h2>
                <section
                  sx={{
                    display: "grid",
                    gridTemplateColumns: [
                      "repeat(1,1fr)",
                      "repeat(2,1fr)",
                      "repeat(3,1fr)",
                    ],
                    gridColumnGap: ["0.85rem", null, "3.00rem"],
                    gridRowGap: ["0.85rem", null, "3.00rem"],
                    "> div": {
                      gridColumn: "initial",
                    },
                  }}
                >
                  {related.map((hit) => (
                    <ResourceCard hit={hit} static />
                  ))}
                </section>
              </section>
            ) : null}
          </Container>
        </div>
      </div>
    </Layout>
  );
};

export const query = graphql`
  query ResourceQuery($id: Craft_QueryArgument) {
    craft {
      entry(id: [$id]) {
        ...resourceVideoFields
        ...resourceStatisticFields
        ...resourceReportFields
        ...resourceInfographicFields
        ...resourceAcademicArticleFields
        ...resourceInsightFields
        ...resourceToolFields
      }
    }
  }
`;
