/** @jsx jsx */
import { jsx } from "theme-ui";
import Container from "@components/Container";
import { useScrollPosition } from "@n8tb1t/use-scroll-position";
import { useState } from "react";
import { Link } from "gatsby";
import { rem } from "@src/theme";
import ArrowRight from "@components/icons/arrowRight";

const HeaderContent = () => {
  return (
    <div
      sx={{
        display: "flex",
        justifyContent: "space-between",
      }}
    >
      <h1
        sx={{
          color: "white",
          my: 0,
          transform: "translateY(8px)",
          fontSize: [rem(24), null, rem(32)],
        }}
      >
        SEES
      </h1>
      <div>
        <Link
          to="/"
          sx={{
            height: "100%",
            display: "inline-flex",
            alignItems: "center",
            px: rem(25),
            textDecoration: "none",
            fontWeight: 600,
            fontSize: [rem(14), null, rem(20)],
          }}
          activeStyle={{ background: "#F4F7FB" }}
          partiallyActive={true}
        >
          Explore
        </Link>
        <Link
          to="/about"
          sx={{
            height: "100%",
            display: "inline-flex",
            alignItems: "center",
            px: rem(25),
            textDecoration: "none",
            fontWeight: 600,
            color: "white",
            fontSize: [rem(14), null, rem(20)],
          }}
          partiallyActive={false}
        >
          About
        </Link>
        <div
          sx={{
            display: "inline-block",
            py: rem(20),
          }}
        >
          <Link
            to="/submit"
            sx={{
              bg: "csiYellow",
              px: rem(14),
              py: rem(10),
              textDecoration: "none",
              fontWeight: 600,
              fontSize: [rem(14), null, rem(20)],
              "&:hover": {
                bg: "csiYellow",
                color: "initial",
              },
            }}
          >
            Submit research
          </Link>
        </div>
      </div>
    </div>
  );
};

const StickyContent = ({ title }) => (
  <div
    sx={{
      display: "flex",
      justifyContent: "space-between",
      color: "white",
      alignItems: "center",
      py: rem(16),
    }}
  >
    <button
      onClick={() => {
        window.history.go(-1);
      }}
      sx={{
        transform: "translateY(-4px)",
        color: "white",
        fontSize: [rem(14), null, rem(18)],
        fontWeight: 600,
        svg: {
          transform: "rotate(180deg) translateY(-5px)",
          marginRight: rem(3),
        },
      }}
    >
      <ArrowRight />
      Back
    </button>

    <h1 sx={{ fontSize: rem(24), my: 0, display: ["none", null, "block"] }}>
      {title}
    </h1>

    <button
      onClick={() => {
        window.scrollTo({ top: 0, behavior: "smooth" });
      }}
      sx={{
        transform: "translateY(-4px)",
        color: "white",
        fontSize: [rem(14), null, rem(18)],
        fontWeight: 600,
        svg: {
          transform: "rotate(270deg) translateY(-5px) translateX(-6px)",
          marginRight: rem(3),
        },
      }}
    >
      <ArrowRight />
      Top
    </button>
  </div>
);

export const DefaultHeader = () => {
  return (
    <div
      sx={{
        bg: "csiDarkGrey",
      }}
    >
      <Container>
        <HeaderContent />
      </Container>
    </div>
  );
};
const StickyHeader = ({ title }: ComponentProps) => {
  const [sticky, setSticky] = useState(false);

  useScrollPosition(
    ({ currPos }) => {
      if (currPos.y >= 80) {
        setSticky(true);
      } else {
        setSticky(false);
      }
    },
    [],
    null,
    true,
    80
  );

  return (
    <div
      sx={{
        position: sticky ? "fixed" : "static",
        bg: "csiDarkGrey",
        width: "100%",
      }}
    >
      <Container>
        {sticky ? <StickyContent title={title} /> : <HeaderContent />}
      </Container>
    </div>
  );
};

interface ComponentProps {
  title?: string;
  stickyMode?: Boolean;
}

const Main = ({ title, stickyMode }: ComponentProps) => {
  if (stickyMode) {
    return <StickyHeader title={title} />;
  } else {
    return <DefaultHeader />;
  }
};
export default Main;
